.landing {
  background: url("/public/bg2.jpg") no-repeat center/cover;
  padding: 20px;
}

.land-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2vmin 17vmin;
}

.land-nav .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 120px;
}

.land-nav .logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
  font-family: fangsong;
}

.land-nav .logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: drop-shadow(1px 1px 3px #ffffff);
}

/* .land-nav {
  display: flex;
} */

.land-body {
  text-align: center;
  align-items: center;
  padding: 0vmin 25vmin;
}

.land-para {
  color: white;
  font-weight: 700;
  font-size: 6vmin;
  margin-top: 10vmin;
  font-family: fangsong;
  text-shadow: 1px 2px 2px rgb(239 11 11 / 60%);
}

.land-para1 {
  color: #ffffff;
  font-family: fangsong;
  font-weight: 700;
  font-size: 5vmin;
  padding: 0 15vmin;
  margin-top: 35vmin;
  text-shadow: 2px 2px 10px rgb(0 0 0 / 60%);
}

.land-body .btns {
  margin-top: 60px;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.buttons__container button {
  background-color: #8e6cda;
  color: white;
  padding: 2vmin 4vmin;
  border-radius: 30px;
  font-size: 3vmin;
  font-weight: bold;
}

.btn1 {
  background-color: #8e6cda;
  color: white;
  padding: 2vmin 4vmin;
  border-radius: 30px;
  font-size: 4vmin;
  font-weight: bold;
  cursor: pointer;
  font-family: fangsong;
  margin-top: 30px;
}

.btns .btn2 {
  padding: 2vmin 5vmin;
}

.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10vmin;
  margin-top: 8vmin;
}

.logo-container img {
  /* filter: drop-shadow(1px 1px 3px #ffffff); */
  width: 150px;
  height: 120px;
  object-fit: none;
}

.land-contact {
  font-size: 5vmin;
  margin-top: 15vmin;
  margin-bottom: 2vmin;
  color: white;
  font-family: fangsong;
  text-shadow: 2px 2px 10px rgb(0 0 0 / 60%);
}

.land-body svg {
  color: white;
  font-size: 3rem;
  margin: 0 15px;
  cursor: pointer;
}

.land-body .opensea__logo {
  margin-left: 25px;
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: black;
  border-radius: 50%;
}

@media (max-width: 660px) {
  .landing {
    padding: 0;
    padding-top: 4vmin;
  }
  .land-body {
    text-align: center;
    align-items: center;
    padding: 10px 0px;
  }
  .landing .land-nav {
    margin: 2vmin 0 0 6vmin;
  }
  .land-nav .logo {
    width: 20vmin;
    height: 20vmin;
    margin: -10px -30px;
    margin-bottom: 30px;
  }
  .land-nav .logo span {
    font-size: 7vmin;
    margin-top: 10vmin;
    margin-left: -4vmin;
    font-family: "Times New Roman", Times, serif;
  }
  .land-para {
    color: white;
    font-weight: 600;
    font-size: 6.5vmin;
    line-height: 8vmin;
    margin-top: 7vmin;
    margin-bottom: 0;
    font-family: "Times New Roman", Times, serif;
  }
  .land-para1 {
    font-size: 4.5vmin;
    padding: 0 6vmin;
    margin-top: 15vmin;
    font-family: "Times New Roman", Times, serif;
  }
  .land-body .btns {
    margin-top: 5px;
  }
  .btn1 {
    font-family: "Times New Roman", Times, serif;
    margin-top: 30px;
    padding: 3.5vmin 7vmin;
    font-size: 4.5vmin;
  }
  .btns .btn2 {
    padding: 3.5vmin 9vmin;
  }
  .logo-container {
    gap: 5vmin;
    margin-top: 4vmin;
  }
  .logo-container > img {
    height: 70px;
    width: 70px;
    object-fit: contain;
  }
  .logo-container > img:last-child {
    height: 90px;
    width: 90px;
  }
  .land-contact {
    font-family: "Times New Roman", Times, serif;
    margin-top: 8vmin;
    margin-bottom: 3vmin;
  }
  .land-body svg {
    font-size: 12vmin !important;
    margin: 0 15px !important;
  }
}
