.nft__collection__container {
  width: 100%;
  min-height: 100vh;
  background-color: black;
}

.nft__collection__container button {
  cursor: pointer;
}

.nft__collection__container .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4vmin 20vmin;
}

.nft__collection__container .navbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.nft__collection__container .navbar .logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
}

.nft__collection__container .navbar .logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* nft__container */

.nft__collection__container .nft__container {
  width: 70%;
  margin: 0 auto;
  text-align: center;
}

.nft__collection__container .nft__container h3 {
  color: white;
  font-size: 6vmin;
  margin: 2vmin 0 3vmin 0;
}

.nft__collection__container .nft__container h1 {
  color: red;
}

.nft__collection__container .nft__container button {
  color: white;
  background-color: #8e6cda;
  padding: 1.5vmin 6vmin;
  font-size: 3.5vmin;
  border-radius: 25px;
  font-weight: bold;
}

/* .tokens__container */

.nft__collection__container .tokens__container {
  width: 80%;
  margin: 0 auto;
  text-align: center;
  margin-top: 10vmin;
}

.nft__collection__container .tokens__container h1 {
  color: white;
  font-size: 6vmin;
  margin: 2vmin 0 6vmin 0;
}

.nft__collection__container .tokens__container .tokens {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20vmin;
}

.nft__collection__container .tokens__container .tokens img {
  width: 17vmin;
  height: 17vmin;
  object-fit: contain;
}

.nft__collection__container .tokens__container .tokens button {
  color: white;
  background-color: #8e6cda;
  padding: 1.5vmin 6vmin;
  font-size: 3.5vmin;
  border-radius: 25px;
  font-weight: bold;
}

/* Responsiveness */

@media screen and (max-width: 660px) {
  .nft__collection__container .navbar {
    padding: 5vmin 4vmin;
  }
  .nft__collection__container .navbar .logo {
    width: 17vmin;
    height: 17vmin;
    margin-left: 7vmin;
  }
  .nft__collection__container .navbar .logo span {
    font-size: 5vmin;
  }
  .nft__collection__container .nft__container {
    width: 95%;
  }
  .nft__collection__container .nft__container h3 {
    margin-top: 20vmin;
    font-size: 8vmin;
  }
  .nft__collection__container .nft__container h1 {
    font-size: 12vmin;
    margin-top: 0;
  }
  .nft__collection__container .nft__container button {
    padding: 3vmin 15vmin;
    font-size: 5vmin;
  }
  /* ------------ */
  .nft__collection__container .tokens__container {
    width: 95%;
    margin-top: 20vmin;
  }
  .nft__collection__container .tokens__container h1 {
    font-size: 9vmin;
  }
  .nft__collection__container .tokens__container .tokens {
    gap: 10vmin;
  }
  .nft__collection__container .tokens__container .tokens img {
    width: 20vmin;
    height: 20vmin;
  }
  .nft__collection__container .tokens__container .tokens button {
    padding: 3vmin 8vmin;
    font-size: 5vmin;
  }
}
