.about-container {
  width: 100%;
  background-color: black;
  font-family: sans-serif;
  padding-bottom: 5vmin;
  /* height: 100vh; */
  padding-bottom: 80px;
}

.about-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4vmin 20vmin;
}

.about-navbar button {
  cursor: pointer;
}

.about-navbar .about-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.about-navbar .about-logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
}

.about-navbar .about-logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.about-navbar button {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 5vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
}

.about-body {
  color: white;
  padding: 0px 15%;
  /* padding: 20%; */
  text-align: center;
  align-items: center;
}

.about-body > h2 {
  font-size: 8vmin;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 2vmin;
  font-family: fangsong;
}

.about-body > p {
  font-size: 18px;
  padding-bottom: 40px;
  font-family: fangsong;
}

.works-para {
  align-items: left;
  text-align: left;
}

.works-para {
  display: flex;
  flex-direction: column;
  /* display: block; */
}

.single-para {
  display: flex;
  /* align-items: center; */
  align-items: baseline;
}

.single-para > span {
  margin-right: 15px;
  font-size: 25px;
}

.single-para > p {
  font-size: 17px;
  font-family: fangsong;
}

@media screen and (max-width: 660px) {
  button {
    border: none;
  }
  .about-body {
    color: white;
    /* padding: 30px 15%; */
    /* padding: 20%; */
    text-align: center;
    align-items: center;
    padding: 10px 20px;
  }
  .about-navbar button {
    padding: 2vmin 6vmin;
    font-size: 3vmin;
    /* display: none; */
  }
  .about-navbar {
    padding: 3vmin 4vmin;
  }
  .about-navbar .about-logo {
    width: 17vmin;
    height: 17vmin;
    margin-left: 7vmin;
  }
  .about-navbar .about-logo span {
    font-size: 5vmin;
  }
  .about-navbar button {
    padding: 3vmin 5vmin;
    font-size: 4vmin;
  }
  .about-body > h2 {
    font-size: 2rem;
    font-weight: 700;
    font-family: "Times New Roman", Times, serif;
  }
  .about-body > p {
    font-size: 17px;
    padding-bottom: 40px;
    font-family: "Times New Roman", Times, serif;
  }
  .works-para {
    align-items: left;
    text-align: left;
  }
  .works-para {
    display: flex;
    flex-direction: column;
    /* display: block; */
  }
  .single-para {
    display: flex;
    /* align-items: center; */
    align-items: baseline;
  }
  .single-para > span {
    margin-right: 15px;
    font-size: 17px;
  }
  .single-para > p {
    font-size: 17px;
  }
}
