.upload-container {
  width: 100%;
  background-color: black;
  font-family: sans-serif;
  padding-bottom: 5vmin;
  /* height: 95vh; */
  /* padding-bottom: 100px; */
}

#createProductFormImage {
  width: 100%;
  overflow: auto;
}

#createProductFormImage > img {
  width: 3vmax;
  margin: 0 0.5vmax;
  padding-bottom: 20px;
  padding-top: -10px;
}

.upload-container button {
  cursor: pointer;
}

.upload-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4vmin 20vmin;
}

.upload-navbar .upload-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.upload-navbar .upload-logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
}

.upload-navbar .upload-logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.upload-navbar button {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 5vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
}

.upload-body {
  color: white;
  text-align: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

input[type="file"] {
  display: none;
}

.custom-file-upload {
  cursor: pointer;
  padding: 10px 20px;
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100px;
  width: 300px;
  text-align: center;
  border-radius: 20px;
  border: 2px solid #8e6cda;
  align-items: center;
  text-align: center;
  margin: 0vmin 0 3vmin 0;
  margin-bottom: 40px;
  color: white;
  font-size: 18px;
}

.upload-input2 {
  /* padding: 2vmin 5vmin; */
  height: 35px;
  width: 300px;
  border-radius: 20px;
  border: 2px solid #8e6cda;
  background-color: transparent;
  text-align: start;
  padding: 5px 20px;
  margin: 0vmin 0 3vmin 0;
  margin-bottom: 40px;
  color: white;
  font-size: 18px;
}

.upload-input3 {
  height: 200px;
  width: 300px;
  border-radius: 20px;
  border: 2px solid #8e6cda;
  background-color: transparent;
  padding: 10px 20px;
  padding: 10px 20px;
  margin: 0vmin 0 3vmin 0;
  font-family: sans-serif;
  color: white;
  font-size: 18px;
}

.about-next-btn {
  padding: 2vmin 7vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
  margin-top: 10px;
  background-color: #8e6cda;
  color: white;

  /* color: #8e6cda;
  background-color: transparent;
  border-radius: 30px;
  font-size: 3vmin;
  font-weight: bold; */
  /* border: 3px solid #8e6cda; */
}
.img-container1 {
  display: flex;
  align-items: center;
  text-align: center;
  margin-bottom: 25px;
}
.img-container1 img {
  height: 50px;
}

.img-container1 span {
  font-size: 22px;
  margin: 5px 10px;
}

@media screen and (max-width: 660px) {
  button {
    border: none;
  }
  .upload-container {
    height: auto;
    padding-bottom: 30px;
  }
  .upload-body {
    /* padding: 30px 15%; */
    padding: 20%;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
  }
  .upload-navbar button {
    padding: 3vmin 9vmin;
    font-size: 4vmin;
    /* display: none; */
  }
  .upload-navbar {
    padding: 3vmin 4vmin;
  }
  .upload-navbar .upload-logo {
    width: 17vmin;
    height: 17vmin;
    margin-left: 7vmin;
  }
  .upload-navbar .upload-logo span {
    font-size: 5vmin;
  }
  .upload-navbar button {
    padding: 3vmin 9vmin;
    font-size: 4vmin;
  }
  .about-next-btn {
    padding: 3vmin 8vmin;
    font-size: 5vmin;
  }

  .img-container1 {
    width: 250px;
  }

  .img-container1 span {
    font-size: 20px;
  }
}
