.token-container {
  width: 100%;
  background-color: black;
  font-family: sans-serif;
  padding-bottom: 5vmin;
  /* height: 95vh; */
  padding-bottom: 100px;
}

.token-container button {
  cursor: pointer;
}

.token-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4vmin 20vmin;
  margin-bottom: 50px;
}

.token-navbar .token-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.token-navbar .token-logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
}

.token-navbar .token-logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.token-navbar button {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 5vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
}

.token-body {
  color: white;
  text-align: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

/* Chrome, Safari, Edge, Opera */

.token-body .token-input2:nth-child(2)::-webkit-outer-spin-button,
.token-body .token-input2:nth-child(2)::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

.token-body .token-input2:nth-child(2)[type="number"] {
  -moz-appearance: textfield;
}

input[type="file"] {
  display: none;
}

.custom-file-token {
  cursor: pointer;
  padding: 10px 20px;
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100px;
  width: 300px;
  text-align: center;
  border-radius: 20px;
  border: 2px solid #8e6cda;
  align-items: center;
  text-align: center;
  margin: 0vmin 0 3vmin 0;
  margin-bottom: 40px;
  color: white;
  font-size: 18px;
}

.token-input2 {
  height: 35px;
  width: 300px;
  border-radius: 20px;
  border: 2px solid #8e6cda;
  background-color: transparent;
  /* align-items: flex-start; */
  text-align: start;
  padding: 5px 20px;
  margin: 0vmin 0 3vmin 0;
  margin-bottom: 40px;
  color: white;
  font-size: 18px;
}

.about-next {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 7vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
  margin-top: 10px;
}

.img-container {
  display: flex;
  align-items: center;
  text-align: center;
  margin-bottom: 25px;
}
.img-container img {
  height: 50px;
}

.img-container span {
  font-size: 22px;
  margin: 5px 10px;
}

@media screen and (max-width: 660px) {
  button {
    border: none;
  }
  .token-body {
    /* padding: 30px 15%; */
    padding: 20%;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
  }
  .token-navbar button {
    padding: 3vmin 9vmin;
    font-size: 4vmin;
    /* display: none; */
  }
  .token-navbar {
    padding: 3vmin 4vmin;
  }
  .token-navbar .token-logo {
    width: 17vmin;
    height: 17vmin;
    margin-left: 7vmin;
  }
  .token-navbar .token-logo span {
    font-size: 5vmin;
  }
  .token-navbar button {
    padding: 3vmin 9vmin;
    font-size: 4vmin;
  }
  .about-next {
    padding: 3vmin 8vmin;
    font-size: 5vmin;
  }

  .img-container {
    width: 240px;
  }

  .img-container span {
    font-size: 20px;
  }
}
