.stakenft__container {
  width: 100%;
  background-color: black;
  font-family: sans-serif;
  min-height: 100vh;
}

.stakenft__container button {
  cursor: pointer;
}

.stakenft__container .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4vmin 20vmin;
}

.stakenft__container .navbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.stakenft__container .navbar .logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
}

.stakenft__container .navbar .logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.stakenft__container .navbar button {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 5vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
}

.stakenft__container .address__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10vmin;
  margin-top: 8vmin;
}

.stakenft__container .address__container .token__address {
  border: 3px solid #fff;
}

.stakenft__container .address__container .wallet__address {
  border: 3px solid #8e6cda;
}

.stakenft__container .address__container .token__address,
.stakenft__container .address__container .wallet__address {
  border-radius: 40px;
  margin: 0 !important;
}

.stakenft__container .address__container .wallet__address input,
.stakenft__container .address__container .token__address input {
  background-color: transparent;
  color: white;
  padding: 3vmin 4vmin;
  outline: none;
  border: none;
  width: 75vmin;
  font-size: 18px;
}

.packages__container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10vmin;
  flex-wrap: wrap;
  margin-top: 25vmin;
  padding-bottom: 10vmin;
}

.packages__container .package {
  padding: 4vmin 0vmin;
  width: 20%;
  color: #8e6cda;
  background-color: #fff;
  text-align: center;
  border-radius: 40px;
  min-height: 70vmin;
}

.packages__container .package:first-child .btn {
  margin-top: 25vmin;
}

.packages__container .package:nth-child(2) .btn {
  margin-top: 19vmin;
}

.packages__container .package .content {
  border-top: 3px solid #8e6cda;
  padding: 0 3vmin;
}

.packages__container .package__middle .content {
  border-top: 3px solid #fff;
}

.packages__container .package h3 {
  color: red;
  font-size: 4.5vmin;
}

.packages__container .package p {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 4vmin;
}

.packages__container .package__middle {
  background-color: #8e6cda;
  color: #fff;
}

.packages__container .package button {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 5vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
  border: none;
}

.packages__container .package__middle button {
  background-color: #fff;
  color: #8e6cda;
}

/* Responsiveness */

@media screen and (max-width: 660px) {
  .stakenft__container .navbar {
    padding: 5vmin 4vmin;
  }
  .stakenft__container .navbar .logo {
    width: 17vmin;
    height: 17vmin;
    margin-left: 7vmin;
  }
  .stakenft__container .navbar .logo span {
    font-size: 5vmin;
  }
  .stakenft__container .navbar button {
    padding: 3vmin 9vmin;
    font-size: 4vmin;
  }
  .stakenft__container .address__container {
    margin-top: 25vmin;
  }
  .stakenft__container .address__container .token__address,
  .stakenft__container .address__container .wallet__address {
    width: 90%;
  }
  .stakenft__container .address__container .wallet__address input,
  .stakenft__container .address__container .token__address input {
    padding: 4vmin 4.5vmin;
    width: 100%;
    font-size: 16px;
  }
  .packages__container .package {
    padding: 7vmin 0vmin;
    width: 70%;
    border-radius: 15px;
    min-height: 70vmin;
  }
  .packages__container .package .btn button {
    padding: 3vmin 9vmin;
    font-size: 5vmin;
  }
  .packages__container .package h3 {
    font-size: 6vmin;
    margin-bottom: 8vmin;
  }
  .packages__container .package p {
    text-align: center;
    font-family: serif;
    font-size: 18px;
    margin-bottom: 7vmin;
  }
}
