.home__container {
  width: 100%;
  background-color: black;
  font-family: sans-serif;
  padding-bottom: 5vmin;
}

.home__container a,
.home__container button {
  cursor: pointer;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4vmin 20vmin;
}

.navbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.navbar .logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
}

.navbar .logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.navbar button {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 5vmin;
  font-size: 3vmin;
  border-radius: 40px;
}

/* balance__container */

.balance__container {
  width: 70%;
  margin: 3vmin auto;
  text-align: center;
}

.balance__container h1 {
  color: white;
  font-size: 8vmin;
  margin: 0;
}

.balance__container h4 {
  color: red;
  margin: 2vmin 0 4vmin 0;
  font-size: 6vmin;
}

.balance__container button {
  color: #8e6cda;
  background-color: transparent;
  padding: 2vmin 4vmin;
  border-radius: 30px;
  font-size: 3vmin;
  font-weight: bold;
  border: 3px solid #8e6cda;
}

/* buttons__container */

.home__container .buttons__container {
  width: 60%;
  margin: 13vmin auto 14vmin auto;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home__container .buttons__container button {
  padding: 2vmin 4vmin;
  border-radius: 30px;
  font-size: 3vmin;
  font-weight: bold;
}

.home__container .buttons__container .btns__left {
  background-color: #8e6cda;
  padding: 20vmin 10vmin;
  border-radius: 30px 0 0 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8vmin;
}

.home__container .buttons__container .btns__left .createnft {
  background-color: transparent;
  color: white;
  border: 3px solid white;
}

.home__container .buttons__container .btns__left .createtoken {
  background-color: white;
  color: #8e6cda;
  border: none;
}

.home__container .buttons__container .btns__right {
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8vmin;
  padding: 20vmin 10vmin;
  border-radius: 30px;
  margin-left: -6vmin;
}

.home__container .buttons__container .btns__right .invest {
  background-color: #8e6cda;
  padding: 2vmin 7vmin;
  color: white;
  border: none;
}

.home__container .buttons__container .btns__right .stakenft {
  background-color: white;
  color: #8e6cda;
  border: 3px solid #8e6cda;
}

/* logos__container */

.logos__container {
  width: 70%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
}

.logos__container img {
  width: 20vmin;
  height: 20vmin;
  object-fit: contain;
}

.logos__container .middle__logos {
  display: flex;
  flex-direction: column;
  gap: 5vmin;
}

.socials__container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: -5vmin auto 0 auto;
  width: 95%;
}

.socials__container svg {
  cursor: pointer;
}

.socials__container img {
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-color: black;
  border-radius: 50%;
}

/* Responsiveness */

@media screen and (max-width: 660px) {
  button {
    border: none;
  }
  .navbar {
    padding: 3vmin 4vmin;
  }
  .navbar .logo {
    width: 17vmin;
    height: 17vmin;
    margin-left: 7vmin;
  }
  .navbar .logo span {
    font-size: 5vmin;
  }
  .navbar button {
    padding: 3vmin 5vmin;
    font-size: 4vmin;
  }
  .balance__container {
    width: 95%;
    margin: 15vmin auto;
  }
  .balance__container h1 {
    font-size: 10vmin;
  }
  .balance__container h4 {
    margin: 4vmin 0 7vmin 0;
    font-size: 9vmin;
  }
  .balance__container button {
    padding: 3.5vmin 7vmin;
    font-size: 4vmin;
  }
  .home__container .buttons__container {
    width: 97%;
    margin: 15vmin auto 35vmin auto;
  }
  .home__container .buttons__container button {
    padding: 3.5vmin 6vmin;
    font-size: 4vmin;
  }
  .home__container .buttons__container .btns__right .invest {
    padding: 3.5vmin 10vmin;
    font-size: 4vmin;
  }
  .home__container .buttons__container .btns__left {
    padding: 20vmin 9vmin 20vmin 4vmin;
  }
  .home__container .buttons__container .btns__right {
    padding: 20vmin 6vmin;
    margin-left: -6vmin;
  }
  .logos__container {
    width: 90%;
  }
  .socials__container {
    margin: 7vmin auto 0 auto;
    width: 90%;
  }
  .socials__container svg {
    font-size: 10vmin !important;
    margin: 0 8px !important;
  }
  .socials__container img {
    width: 35px;
    height: 35px;
    margin-left: 5px;
  }
}
