.pay__container {
  width: 100%;
  background-color: black;
  font-family: sans-serif;
  min-height: 100vh;
}

.pay__container button {
  cursor: pointer;
}

.pay__container .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4vmin 20vmin;
}

.pay__container .navbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.pay__container .navbar .logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
}

.pay__container .navbar .logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.pay__container .navbar button {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 5vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
}

.pay__container h1 {
  color: #03b661;
  text-align: center;
  font-size: 8vmin;
}

/* amount__container */

.amount__container {
  display: grid;
  place-items: center;
  margin-top: 15vmin;
}

.amount__container p {
  color: red;
  font-size: 3vmin;
}

.amount__container .input__container {
  border-radius: 20px;
  border: 2px solid #8e6cda;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0vmin 0 3vmin 0;
}

.amount__container .input__container img {
  object-fit: contain;
  width: 50px;
  height: 50px;
  padding: 5px 10px;
  border-right: 2px solid #8e6cda;
}

.amount__container .input__container input {
  background-color: transparent;
  color: white;
  padding: 3vmin 3vmin;
  outline: none;
  border: none;
  width: 45vmin;
  font-size: 18px;
}

/* Chrome, Safari, Edge, Opera */

.amount__container .input__container input::-webkit-outer-spin-button,
.amount__container .input__container input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

.amount__container .input__container input[type="number"] {
  -moz-appearance: textfield;
}

.amount__container button {
  color: white;
  background-color: #8e6cda;
  padding: 1.5vmin 6vmin;
  font-size: 3.5vmin;
  border-radius: 25px;
  font-weight: bold;
}

/* Token Card */

.pay__container .tokens__container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10vmin;
  width: 80%;
  flex-wrap: wrap;
  margin: 0 auto;
  padding-bottom: 8vmin;
}

.pay__container h2 {
  color: white;
  text-align: center;
  font-size: 7vmin;
  margin-bottom: 10vmin;
  margin-top: 10vmin;
}

.tokencard__container {
  background-color: #1d1a1a;
  padding: 5vmin 3vmin;
  min-height: 30vmin;
  border-radius: 20px;
  width: 20%;
}

.tokencard__container .img__container {
  width: 100%;
  height: 30vmin;
  margin-bottom: 5vmin;
  border-radius: 35px;
}

.tokencard__container .img__container img {
  width: 100%;
  height: 100%;
  /* object-fit: contain; */
  border-radius: inherit;
}

.tokencard__container .token__content h5 {
  color: white;
  margin-bottom: 3vmin;
  font-size: 3.5vmin;
  font-family: "Times New Roman", Times, serif;
}

.tokencard__container .token__content p {
  font-family: "Times New Roman", Times, serif;
  color: white;
  font-size: 25px;
}

/* Responsiveness */

@media screen and (max-width: 660px) {
  .pay__container .navbar {
    padding: 5vmin 4vmin;
  }
  .pay__container .navbar .logo {
    width: 17vmin;
    height: 17vmin;
    margin-left: 7vmin;
  }
  .pay__container .navbar .logo span {
    font-size: 5vmin;
  }
  .pay__container .navbar button {
    padding: 3vmin 9vmin;
    font-size: 4vmin;
  }
  .pay__container h1 {
    margin-top: 20vmin;
    font-size: 9vmin;
  }
  /* amount__container */
  .amount__container {
    margin-top: 20vmin;
  }
  .amount__container p {
    font-size: 4vmin;
    padding: 0 5vmin;
    text-align: center;
  }
  .amount__container .input__container {
    border-radius: 20px;
    margin: 3vmin 0 6vmin 0;
  }
  .amount__container .input__container img {
    width: 50px;
    height: 50px;
  }
  .amount__container .input__container input {
    padding: 2vmin 3vmin;
    outline: none;
    border: none;
    width: 55vmin;
    font-size: 18px;
  }
  .amount__container button {
    padding: 3vmin 15vmin;
    font-size: 5vmin;
  }
  .tokencard__container {
    min-height: 90vmin;
    width: 100%;
    padding: 10vmin 7vmin;
  }
  .tokencard__container .img__container {
    height: 50vmin;
  }
  .tokencard__container .token__content h5 {
    margin-bottom: 3vmin;
    font-size: 7vmin;
  }
  .tokencard__container .token__content p {
    font-size: 7vmin;
  }
  .tokencard__container .token__content p span {
    font-size: 7vmin;
  }
  .pay__container h2 {
    margin-top: 25vmin;
  }
}
