.withdrawpay__container {
  width: 100%;
  background-color: black;
  font-family: sans-serif;
  min-height: 100vh;
}

.withdrawpay__container button {
  cursor: pointer;
}

.withdrawpay__container .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4vmin 20vmin;
}

.withdrawpay__container .navbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.withdrawpay__container .navbar .logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
}

.withdrawpay__container .navbar .logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.withdrawpay__container .navbar button {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 5vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
}

.withdrawpay__container .amount__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 0vmin;
}

.withdrawpay__container .amount__container h3 {
  color: #11bb11;
  font-size: 5vmin;
}

.withdrawpay__container .amount__container p {
  color: #f11f1f;
  margin: 3vmin 0 8vmin 0;
  font-size: 3vmin;
}

.withdrawpay__container .amount__container .input__container {
  border: 3px solid #8e6cda;
}

.withdrawpay__container .amount__container .input__container {
  border-radius: 40px;
  margin: 0 !important;
}

.withdrawpay__container .amount__container .input__container input {
  background-color: transparent;
  color: white;
  padding: 2.5vmin 4vmin;
  outline: none;
  border: none;
  width: 60vmin;
  font-size: 18px;
}

/* Chrome, Safari, Edge, Opera */

.withdrawpay__container .input__container input::-webkit-outer-spin-button,
.withdrawpay__container .input__container input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

.withdrawpay__container .input__container input[type="number"] {
  -moz-appearance: textfield;
}

.withdrawpay__container .amount__container button {
  margin-top: 5vmin;
  border: 2px solid #8e6cda;
  color: #8e6cda;
  background-color: white;
  border-radius: 30px;
  font-size: 3.5vmin;
  font-weight: bold;
  padding: 1.5vmin 6vmin;
  font-weight: bold;
}

/* Responsiveness */

@media screen and (max-width: 660px) {
  .withdrawpay__container .navbar {
    padding: 5vmin 4vmin;
  }
  .withdrawpay__container .navbar .logo {
    width: 17vmin;
    height: 17vmin;
    margin-left: 7vmin;
  }
  .withdrawpay__container .navbar .logo span {
    font-size: 5vmin;
  }
  .withdrawpay__container .navbar button {
    padding: 3vmin 9vmin;
    font-size: 4vmin;
  }
  .withdrawpay__container .amount__container {
    margin-top: 13vmin;
  }
  .withdrawpay__container .amount__container h3 {
    font-size: 7vmin;
    padding: 0 4vmin;
    text-align: center;
  }
  .withdrawpay__container .amount__container p {
    margin: 8vmin 2vmin 7vmin 2vmin;
    text-align: center;
    font-size: 4.5vmin;
  }
  .withdrawpay__container .amount__container .input__container {
    border: 2px solid #8e6cda;
  }
  .withdrawpay__container .amount__container .input__container {
    border-radius: 30px;
    width: 80%;
  }
  .withdrawpay__container .amount__container .input__container input {
    padding: 4vmin 4.5vmin;
    width: 100%;
    font-size: 16px;
    text-align: center;
  }
  .withdrawpay__container .amount__container button {
    padding: 3.5vmin 9vmin;
    margin-top: 10vmin;
    font-size: 5vmin;
  }
}
