.invest__container {
  width: 100%;
  background-color: black;
  font-family: sans-serif;
  min-height: 100vh;
}

.invest__container button {
  cursor: pointer;
}

.invest__container .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4vmin 20vmin;
}

.invest__container .navbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.invest__container .navbar .logo span {
  color: white;
  font-size: 6vmin;
  margin-top: 6vmin;
  margin-left: -3vmin;
}

.invest__container .navbar .logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.invest__container .navbar button {
  color: white;
  background-color: #8e6cda;
  padding: 2vmin 5vmin;
  font-size: 3vmin;
  border-radius: 40px;
  font-weight: bold;
}

/* .amount__container  */

.invest__container .amount__container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5vmin;
  margin-top: 8vmin;
}

.invest__container .amount__container .input__container {
  border-radius: 40px;
  border: 2px solid #8e6cda;
  margin: 0 !important;
}

.invest__container .amount__container .input__container input {
  background-color: transparent;
  color: white;
  padding: 2.5vmin 3vmin;
  outline: none;
  border: none;
  width: 45vmin;
  font-size: 18px;
}

/* Chrome, Safari, Edge, Opera */

.invest__container
  .amount__container
  .input__container
  input::-webkit-outer-spin-button,
.invest__container
  .amount__container
  .input__container
  input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

.invest__container .amount__container .input__container input[type="number"] {
  -moz-appearance: textfield;
}

.invest__container .amount__container button {
  color: white;
  background-color: #8e6cda;
  padding: 1.5vmin 6vmin;
  font-size: 3.5vmin;
  border-radius: 25px;
  font-weight: bold;
}

/* machine___container */

.machine__container {
  display: grid;
  place-items: center;
  margin-top: 12vmin;
}

.machine__container p {
  color: white;
  font-size: 4vmin;
}

.machine__container .input__container {
  border-radius: 20px;
  border: 2px solid #8e6cda;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0vmin 0 3vmin 0;
}

.machine__container .input__container img {
  object-fit: contain;
  width: 40px;
  height: 40px;
  padding: 14px 17px;
  border-right: 2px solid #8e6cda;
}

.machine__container .input__container input {
  background-color: transparent;
  color: red;
  padding: 2.5vmin 3vmin;
  outline: none;
  border: none;
  width: 45vmin;
  font-size: 30px;
  text-align: center;
}

/* Chrome, Safari, Edge, Opera */

.machine__container .input__container input::-webkit-outer-spin-button,
.machine__container .input__container input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

.machine__container .input__container input[type="number"] {
  -moz-appearance: textfield;
}

/* Chart Container */

.invest__container .chart__container {
  width: 50%;
  margin: 10vmin auto;
}

.invest__container .note__container {
  text-align: center;
  padding: 10vmin 0;
  width: 30%;
  margin: 0 auto;
}

.invest__container .note__container h5 {
  color: red;
  font-size: 5vmin;
  margin: 0;
}

.invest__container .note__container p {
  color: white;
  font-size: 20px;
  line-height: 30px;
}

/* Responsiveness */

@media screen and (max-width: 660px) {
  .invest__container .navbar {
    padding: 5vmin 4vmin;
  }
  .invest__container .navbar .logo {
    width: 17vmin;
    height: 17vmin;
    margin-left: 7vmin;
  }
  .invest__container .navbar .logo span {
    font-size: 5vmin;
  }
  .invest__container .navbar button {
    padding: 3vmin 9vmin;
    font-size: 4vmin;
  }
  .invest__container h1 {
    margin-top: 20vmin;
    font-size: 9vmin;
  }
  .invest__container .amount__container {
    margin-top: 25vmin;
  }
  .invest__container .amount__container {
    flex-direction: column;
    gap: 5vmin;
  }
  .invest__container .amount__container .input__container {
    border-radius: 25px;
  }
  .invest__container .amount__container .input__container input {
    padding: 5vmin 7vmin;
    width: 70vmin;
    font-size: 17px;
  }
  .invest__container .amount__container button {
    padding: 3vmin 12vmin;
    font-size: 5vmin;
  }
  .machine__container {
    margin-top: 20vmin;
  }
  .machine__container p {
    font-size: 5vmin;
  }
  .invest__container .chart__container {
    width: 80%;
    margin: 0 auto !important;
  }
  .invest__container .note__container {
    padding: 10vmin 0;
    width: 85%;
  }
  .invest__container .note__container h5 {
    font-size: 7vmin;
  }
  .invest__container .note__container p {
    font-size: 4.5vmin;
    line-height: 25px;
  }
}
